<template>
  <img :src="url" alt="..." />
</template>
<script>
export default {
  name: "imgComp",
  props: {
    src: {
      type: String,
      required: false,
      default: () => {
        return this.url;
      },
    },
  }, // url
  data() {
    return {
      // url: require("../../public/img/indexBg/loading.gif"), //  loading.gif
      url: "https://pic.imgdb.cn/item/65abdd55871b83018af558ee.gif",
    };
  },
  mounted() {
    var newImg = new Image();
    newImg.src = this.src;
    newImg.onerror = () => {
      // 
      newImg.src = this.url;
    };
    newImg.onload = () => {
      // img
      this.url = newImg.src;
    };
  },
};
</script>
