/**
 * @Event 
 * @description: 
 * */
export function imgFormat(url) {
  if (typeof url === "string") {
    return process.env.NODE_ENV === "production" ? "/msx" + url : url;
  }
  if (typeof url === "object") {
    return process.env.NODE_ENV === "production" ? ["/msx" + url[0]] : url;
  }
}

export default { imgFormat };
